<template>
  <div>

    <launch_scheduler_btn>
    </launch_scheduler_btn>

  </div>
</template>


<script>
import launch_scheduler_btn from './launch_scheduler_btn'

export default {
  components: {
    launch_scheduler_btn

  },
  data: () => {
    return {
      schedule_type: 'scheduler',
      scheduler_text: 'Please select days and time',
      checkbox_dates:[],
      e4: null 
    }
  },
  methods: {
    schedulerFrequencyText () {
      var time = '<time>'
      if (this.e4) time = this.e4.toString()

      this.scheduler_text = 'Scheduled to run @' + time + ' on ' 
      var days = ''

      for (var i=0; i<this.checkbox_dates.length; i++) {
        days+=this.checkbox_dates[i]
        if (i < this.checkbox_dates.length-1) days+=', '
      }
      if (!days) days='<days>'
      this.scheduler_text+=days
      this.scheduler_text+='.'
    }
  }
}
</script>

