<template>
  <div>
  <v-tabs fixed-tabs v-model="cTab">
    <v-tab v-for="tab in tabs" :key="tab.id" @click="switchTab(tab.id, 'click')">
      {{tab.title}}
    </v-tab>
    
    <v-tabs-items v-model="cTab">

      <v-tab-item>
        <jobs_all></jobs_all>
      </v-tab-item>

      <v-tab-item v>
        <scheduled_table></scheduled_table>
      </v-tab-item>

    </v-tabs-items>

  </v-tabs>

  </div>

</template>

<script>

import jobs_all from './jobs_past'
import scheduled_table from './scheduled_table'

export default {
  data () {
    return {
      cTab: 0,
      tabs: [
        {
          title: 'All Jobs',
          key: 'all-jobs',
          id:0
        },
        {
          title: 'Scheduled',
          key: 'scheduled',
          id:1
        }
      ]
    }
  },
  components: {
    jobs_all,
    scheduled_table
  },
  mounted: function() {
    var tab = this.$route.query['tab'];
    if (tab) {
      for(var i=0; i<this.tabs.length; i++) {
        if (this.tabs[i]['key'] == tab) {
          this.cTab = i 
          break
        }
      }
      console.log("tab" + this.cTab)
    }
    this.switchTab(this.cTab, 'load');

  },
  methods : {
    switchTab (tabId, actionType) {
      if (actionType == 'load') {
        if (tabId === undefined || (!(this.tabs.length -1 >= parseInt(tabId))))
          tabId = 0;
          this.cTab = tabId;
      } else if (actionType == 'click') {
        this.cTab = tabId
        var tab_url_key = this.tabs[this.cTab]['key']
        this.$router.replace(window.location.pathname+'?tab='+tab_url_key)
        //if ('URLSearchParams' in window) {

        //var searchParams = new URLSearchParams(window.location.search)
        //searchParams.set("tab", tabId);
        //var newRelativePathQuery = window.location.pathname + '?' + searchParams.toString();
        //this.$router.replace(newRelativePathQuery);
        //}
      }
    }
  }
}
</script>

