<style>
  .currentJob{
    background-color: #686868;
  }
</style>
<template>
<v-container grid-list-md text-xs-center>
  <v-layout row wrap>
    <v-flex xs12 sm12 md8 lg8>
      <v-card>
        <v-toolbar color="indigo" dark>
          <v-toolbar-title><h1>Jobs</h1></v-toolbar-title>

          <v-spacer></v-spacer>

      <v-chip v-model="completed_chip" 
        close
        @input="removeChip(1)"
        color="success"
        text-color="white"
        >
        <v-avatar>
          <v-icon>check_circle</v-icon>
        </v-avatar>
        Completed
      </v-chip>

      <v-chip v-model="error_chip" 
        close
        @input="removeChip(2)"
        color="error"
        text-color="white" 
        >
        <v-avatar>
          <v-icon>error</v-icon>
        </v-avatar>
       Error 
      </v-chip>

      <v-chip v-model="inprogress_chip" 
        close
        @input="removeChip(0)"
        color="primary"
        text-color="white" 
        >
        <v-avatar>
          <v-icon>autorenew</v-icon>
        </v-avatar>
       In-Progress
      </v-chip>

      <v-chip v-model="queued_chip" 
        close
        @input="removeChip(-1)"
        color="blue lighten-2"
        text-color="white" 
        >
        <v-avatar>
          <v-icon>list</v-icon>
        </v-avatar>
       Queued
      </v-chip>



          <launch_job_btn></launch_job_btn>
        </v-toolbar>
        <v-data-table
        :headers="headers_job"
        :items="$store.getters.filtered_jobs"
        class="elevation-1"
        v-bind:pagination.sync="job_pagination"
        >
        <template v-slot:no-data>
          <v-alert :value="true" color="error" icon="error">
            No Jobs to track
          </v-alert>
        </template v-slot:no-data>

        <template v-slot:items="props">
          <tr v-on:click="showJobTasks(props.item.id)"  :class="$store.getters.cjob_id==props.item.id ? 'currentJob': ''">
          <td class="text-xs-left">{{ props.item.id }}</td>
          <td class="text-xs-left">{{ props.item.name }}</td>
          <td class="text-xs-left">{{ props.item.stage }}</td>
            <td class="text-xs-left">
              <v-tooltip top>
                <template v-slot:activator="{ on }">
              <v-icon 
                v-show="props.item.status==1"
                color="success"class="mr-2"
                v-on="on"
                >
               check 
              </v-icon>
              <v-icon 
                v-show="props.item.status==2" 
                color="error" class="mr-2"
                v-on="on"
                >
                error
              </v-icon>
              <v-icon 
                v-show="props.item.status==0&&props.item.stage=='Queued'" 
                color="blue lighen-2" class="mr-2"
                v-on="on"
                >
                list
              </v-icon>

              <font-awesome-icon 
                v-show="props.item.status==0&&props.item.stage!='Queued'"
                :style="{color:'#2196f3'}"
                icon="circle-notch" 
                size="2x" spin
                v-on="on"
              font-awesome-icon/>

                </template>
                <span>{{ props.item.msg }} </span>
              </v-tooltip>

            </td>
          </tr>
        </template>
      </v-data-table>
      </v-card>
    </v-flex>


    <v-flex x12 sm12 md4 lg4>
      <v-card>
        <v-toolbar color="indigo" dark>
          <v-toolbar-title>Job {{$store.getters.cjob_id}}'s Tasks</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-tooltip top>
            <template v-slot:activator="{ on }">
              <v-btn icon v-on="on">
                <v-icon @click="stop_job">stop</v-icon>
              </v-btn>
            </template>
            <span>Stop ALL job's tasks</span>
          </v-tooltip>

        </v-toolbar>

        <v-data-table
          :headers="headers_task"
          :items="$store.getters.job_tasks"
          class="elevation-1"
          v-bind:pagination.sync="task_pagination"
          >
          <template v-slot:no-data>
            <v-alert :value="true" color="error" icon="error">
              Select a job to view its tasks
            </v-alert>
          </template v-slot:no-data>

          <template v-slot:items="props">
            <td>{{ props.item.id }}</td>
            <td>{{ props.item.name }}</td>
            <td>{{ props.item.time_completed }}</td>

            <td class="px-0">
              <v-tooltip top>
                <template v-slot:activator="{ on }">

              <v-icon 
                v-show="props.item.status==1"
                color="success"class="mr-2"
                v-on="on"
                >
               check 
              </v-icon>
              <v-icon 
                v-show="props.item.status==2" 
                color="error" class="mr-2"
                v-on="on"
                >
                error
              </v-icon>

              <v-icon
                v-show="props.item.status==3"
                color="warning" class="mr-2"
                v-on="on"
                >
                warning
              </v-icon>

              <font-awesome-icon 
                v-show="props.item.status==0"
                :style="{color:'#2196f3'}"
                icon="circle-notch" 
                size="2x" spin
                v-on="on"
              font-awesome-icon/>
                </template>
                <span>{{ props.item.msg }} </span>
              </v-tooltip>


            </td>
          </template>
        </v-data-table>
      </v-card>
      </v-flex>
  </v-layout>
</v-container>
</template>
<script>
import launch_job_btn from './launch_job_btn'

export default {
  components: {
    launch_job_btn
  },
  mounted () {
    this.$store.commit("SET_CJOB_ID", null)

    var status = this.$route.query.job_status

    
    switch (status) {
      case '-1':
        this.queued_chip = true
        break
      case '0':
        this.inprogress_chip = true
        break
      case '1':
        this.completed_chip = true
        break
      case '2':
        this.error_chip = true
        break
      default:
        for(var i=-1; i<3; i++) this.removeChip(i)
    }
    this.filterJobs()
  },
  props: ['headers_task', 'headers_job'],
  methods: {
    filterJobs() {
        var stat = this.$route.query.job_status
        if (stat) stat = parseInt(stat)
        this.$store.commit("SET_JOB_FILTER", stat)
    },
    stop_job () {
      if (this.$store.getters.cjob_id) {
        var msg = {
          route: "bd.@md.slave.job.stop",
          data: {
            job_id: this.$store.getters.cjob_id,
            msg: "Job stopped by CP User"
          }
        }


        console.log("JOB STOP MSG: "+JSON.stringify(msg))
        var s = this.$socket.emit('forward', msg)
        if (s.connected) {
          console.log("^worked!")//alert here
        }
      }
    },

    removeChip (status) {
      if (status != undefined) {
        switch(status) {
          case -1:
            this.queued_chip = false
            break
          case 0:
            this.inprogress_chip = false
            break
          case 1:
            this.completed_chip = false
            break
          case 2:
            this.error_chip = false
            break
        }
      }
      var r = new URL(window.location.href);
      r.searchParams.delete('job_status');
      this.$router.replace(r.pathname + r.search)
      this.filterJobs()
    },
    showJobTasks (job_id) {
      if (this.job_tasks === undefined) this.jobs_tasks = []
      this.$store.commit('SET_CJOB_ID', job_id)
    }
  },
  data: () => {
    return {
      job_pagination: {
        sortBy: 'id', descending: true, rowsPerPage:6
      },
      task_pagination: {
        sortBy: 'id', descending: true, rowsPerPage: 6
      },
      error_chip: false,
      completed_chip: false,
      inprogress_chip:false,
      queued_chip: false,

      dialog: false,
    }
  }
}
</script>
